import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Router } from '@angular/router';

import { Authresonse, AuthModel } from './auth.model';
import { Subject, BehaviorSubject } from 'rxjs';
import { tap, catchError } from 'rxjs/operators';

import { User } from './user.model';

import { baseUrl } from '../../config/variables';

@Injectable({
  providedIn: 'root',
})
export class AuthService {
  user = new BehaviorSubject<User>(null);
  error = new Subject<String>();

  constructor(private http: HttpClient, private router: Router) {}

  postForm(endPoint: String, form: AuthModel) {
    return this.http.post<Authresonse>(baseUrl + '/' + endPoint, form).pipe(
      tap((resData) => {
        const userData = new User(resData.token);
        this.user.next(userData);
        // localStorage.setItem('userData', JSON.stringify(userData));
      }),
      catchError((err) => {
        console.log(err);
        this.error.next(err.error.message);
        return this.error;
      })
    );
  }

  logout() {
    this.user.next(null);
    this.router.navigate(['/auth/login']);
  }
}
